






























































import Vue from 'vue';
import { summary } from '@/service/char_summary';


export default Vue.extend({
  data: () => ({
    vocals: [] as any[],
    consonants: [] as any[]
  }),
  methods: {
    async loadData() {
      this.vocals = await summary('vocal');
      this.consonants = await summary('konsonan');
    }
  },
  mounted() {
    this.loadData();
  }
});
